

<template>
  <div class="vip-appointment">
    <van-sticky :offset-top="0.00000001">
      <van-nav-bar title="贵宾预约" left-arrow @click-left="onClickLeft" />
    </van-sticky>
    <!-- 主页面 -->
    <div class="edit-form">
      <div class="tag-title">拜访信息</div>
      <van-form
        :show-error="false"
        :model="visitorInfo"
        @submit="handleSubmit"
        :readonly="params.disabled"
        ref="visitorInfo"
      >
        <div class="visitor-info">
          <van-field
            name="visitorReason"
            v-model="visitorInfo.visitorReason"
            required
            label="拜访事由"
            maxlength="50"
            placeholder="请输入拜访事由"
            :rules="[{ required: true, message: '请输入拜访事由' }]"
          />
          <van-field
            :clickable="!params.disabled"
            @click="
              if (!params.disabled) {
                showPickerStart = true
              }
            "
            readonly
            name="visitStartTime"
            required
            v-model="visitorInfo.visitStartTime"
            label="拜访开始"
            placeholder="请选择开始时间"
            :rules="[{ required: true, message: '请选择开始时间' }]"
          />
          <van-popup v-model="showPickerStart" position="bottom">
            <van-datetime-picker
              type="datetime"
              @confirm="onConfirmStart"
              @cancel="showPickerStart = false"
              :min-date="disabledTime('startMin')"
              :max-date="disabledTime('startMax')"
            />
          </van-popup>
          <van-field
            :clickable="!params.disabled"
            @click="
              if (!params.disabled) {
                showPickerEnd = true
              }
            "
            readonly
            required
            name="visitEndTime"
            v-model="visitorInfo.visitEndTime"
            label="拜访结束"
            placeholder="请选择结束时间"
            :rules="[{ required: true, message: '请选择结束时间' }]"
          />
          <van-popup v-model="showPickerEnd" position="bottom">
            <van-datetime-picker
              type="datetime"
              @confirm="onConfirmEnd"
              @cancel="showPickerEnd = false"
              :min-date="disabledTime('endMin')"
              :max-date="disabledTime('endMax')"
            />
          </van-popup>
          <van-field
            v-model="visitorInfo.visitorCompany"
            name="visitorCompany"
            label="所属单位"
            maxlength="50"
            placeholder="请填写访客所属单位"
          />
        </div>
      </van-form>
      <div class="tag-title">访客信息</div>
      <van-swipe-cell v-for="(item, i) in userList" :key="i">
        <div
          @click="handleEdit(item, i)"
          class="
            van-cell van-cell--clickable van-cell--center van-cell--borderless
            van-contact-card van-contact-card--edit
          "
        >
          <van-image
            round
            fit="cover"
            :src="
              (item.fileList && item.fileList[0] && item.fileList[0].content) ||
              require('../../../assets/image/home/default-touxiang.png')
            "
            alt="图片加载失败"
          />
          <div
            class="
              van-cell__value van-cell__value--alone
              van-contact-card__value
              title-primary
            "
          >
            <div>{{ item.visitorName }}</div>
            <div>{{ item.visitorPhoneNbr }}</div>
          </div>
          <i class="van-icon van-icon-arrow van-cell__right-icon" />
        </div>
        <template #right v-if="!params.disabled">
          <van-button
            square
            type="danger"
            text="删除"
            @click="onDetailSubmit('del', i)"
          />
        </template>
      </van-swipe-cell>
      <van-contact-card
        v-if="!params.disabled"
        @click="handleAddDetail"
        add-text="新增访客"
      />
      <div class="footer-btn" v-if="verifyTime()">
        <van-icon
          v-if="!params.disabled"
          name="save iconfont"
          :class-prefix="'icon'"
          size="50"
          @click="$refs.visitorInfo.submit()"
        />
        <!-- <van-icon
          name="weixin iconfont"
          v-if="visitorInfoId && this.userList.length > 0"
          :class-prefix="'icon'"
          size="50"
          @click="handleWeChat"
        /> -->
        <van-icon
          v-if="visitorInfoId && this.userList.length > 0"
          name="duanxin iconfont"
          :class-prefix="'icon'"
          size="50"
          @click="handleSMS"
        />
        <van-icon
          v-if="visitorInfoId && this.userList.length > 0"
          name="lianjie iconfont"
          :class-prefix="'icon'"
          size="50"
          @click="copyLink"
        />
      </div>
    </div>
    <!-- 访客信息页面 -->
    <div v-show="editState" class="detail">
      <van-sticky :offset-top="0.00000001">
        <van-nav-bar
          :right-text="!params.disabled ? '保存' : ''"
          title="访客信息"
          left-arrow
          @click-left="onClickLeft"
          @click-right="$refs.userInfo.submit()"
        />
      </van-sticky>
      <div class="edit-form">
        <van-form
          :show-error="false"
          @submit="onDetailSubmit($event)"
          :model="userInfo"
          :readonly="params.disabled"
          ref="userInfo"
        >
          <div class="visitor-info">
            <van-field name="fileList" label="人脸上传">
              <template #input>
                <van-uploader
                  :readonly="params.disabled"
                  :deletable="!params.disabled"
                  @delete="userInfo.picData = ''"
                  v-model="userInfo.fileList"
                  :before-read="beforeRead"
                  accept="image/jpeg,image/jpg,image/png"
                  :max-count="1"
                />
              </template>
            </van-field>
            <van-field
              v-model="userInfo.visitorName"
              placeholder="请填写访客姓名"
              name="visitorName"
              label="访客姓名"
              required
              maxlength="8"
              :rules="[{ required: true, message: '请填写访客姓名' }]"
            />
            <van-field
              v-model="userInfo.visitorPhoneNbr"
              name="visitorPhoneNbr"
              label="手机号码"
              required
              placeholder="请填写访客手机号码"
              type="tel"
              maxlength="11"
              :rules="[
                { required: true, message: '请填写访客手机号码' },
                {
                  pattern:
                    /^(13[0-9]|14[01456879]|15[0-35-9]|16[2567]|17[0-8]|18[0-9]|19[0-35-9])\d{8}$/,
                  message: '请输入正确格式的手机号码'
                }
              ]"
            />
            <van-field
              :clickable="!params.disabled"
              @click="
                if (!params.disabled) {
                  showPickerType = true
                }
              "
              readonly
              name="voucherTypeName"
              v-model="userInfo.voucherTypeName"
              label="证件类型"
              placeholder="请选择证件类型"
            />
            <van-popup v-model="showPickerType" position="bottom">
              <van-picker
                title="证件类型"
                show-toolbar
                :columns="showPickerTypeList.map((item) => item.dicDisplay)"
                @cancel="showPickerType = false"
                @confirm="onConfirmType"
              />
            </van-popup>
            <van-field
              :readonly="
                userInfo.voucherTypeName === '身份证' || params.disabled
              "
              @click="
                if (!params.disabled && userInfo.voucherTypeName === '身份证') {
                  numberShow = true
                }
              "
              :clickable="!params.disabled"
              v-model="userInfo.voucherCode"
              name="voucherCode"
              label="证件号码"
              maxlength="50"
              placeholder="请填写访客证件号码"
            />
            <van-number-keyboard
              :show="numberShow"
              extra-key="X"
              close-button-text="完成"
              @blur="numberShow = false"
              @input="
                (val) => {
                  userInfo.voucherCode = userInfo.voucherCode + val
                }
              "
              @delete="
                userInfo.voucherCode = userInfo.voucherCode.substr(
                  0,
                  userInfo.voucherCode.length - 1
                )
              "
            />
            <van-field name="visitSex" label="访客性别">
              <template #input>
                <van-radio-group
                  :disabled="params.disabled"
                  v-model="userInfo.visitSex"
                  direction="horizontal"
                >
                  <van-radio
                    :name="item.dicValue"
                    v-for="item in visitSexList"
                    :key="item.dicValue"
                  >
                    {{ item.dicDisplay }}
                  </van-radio>
                </van-radio-group>
              </template>
            </van-field>
            <van-field
              v-model="userInfo.plate"
              maxlength="8"
              label="车牌号码"
              name="plate"
              placeholder="请填写车牌号码"
            />
            <van-field
              v-model="userInfo.carryArticle"
              name="carryArticle"
              label="携带物品"
              maxlength="100"
              placeholder="请填写访客携带物品"
            />
            <van-field
              v-model="userInfo.remark"
              name="remark"
              label="备注"
              rows="2"
              autosize
              type="textarea"
              show-word-limit
              maxlength="100"
              placeholder="请填写备注"
            />
          </div>
        </van-form>
      </div>
    </div>
    <div class="footer-placeholder" />
  </div>
</template>
<script>
import vm from '@/main'
import {
  addVisitorInfo,
  modifyVisitorInfo,
  qryVisitorInfoDetail
} from '@/api/visitor' // 新增
import { mapState } from 'vuex'
export default {
  name: 'VIPappointment',
  data() {
    return {
      numberShow: false, //身份证属性
      checkbox: false,
      editState: false, // 显示状态
      showPickerTypeList: [
        {
          dicDisplay: '身份证',
          dicValue: '1'
        },
        {
          dicDisplay: '香港身份证',
          dicValue: '2'
        },
        {
          dicDisplay: '澳门身份证',
          dicValue: '3'
        },
        {
          dicDisplay: '港澳通行证',
          dicValue: '4'
        },
        {
          dicDisplay: '台湾居民定居证',
          dicValue: '5'
        },
        {
          dicDisplay: '其他',
          dicValue: '6'
        }
      ], //身份证类型字典
      visitSexList: [
        {
          dicDisplay: '男',
          dicValue: '0'
        },
        {
          dicDisplay: '女',
          dicValue: '1'
        }
      ], //性别字典
      showPickerType: false, // 类型modal
      showPickerStart: false, // 开始时间
      showPickerEnd: false, // 结束时间
      token: '', //分享token
      visitorInfoId: '', //访问id
      visitorInfo: {}, //头表信息
      userList: [], //访客列表
      userInfo: {
        //访客单独信息
        fileList: [],
        visitorName: '',
        visitorPhoneNbr: '',
        voucherTypeName: '身份证',
        voucherCode: '',
        visitSex: '',
        carryArticle: '',
        plate: '',
        remark: ''
      },
      params: {} //路由参数
    }
  },
  methods: {
    verifyTime() {
      return (
        !this.params.disabled ||
        new Date() < new Date(this.visitorInfo.visitEndTime)
      )
    },
    async init() {
      //查询数据
      this.params = this.$util.getRouteParams(this.$route) // 获取页传入的参数 or 设置参数
      this.token = this.params.token //分享token
      this.visitorInfoId = this.params.vipVisitId //访问id
      if (this.visitorInfoId) {
        qryVisitorInfoDetail({
          visitorInfoId: this.visitorInfoId
        }).then((res) => {
          if (res.code === 200) {
            const data = res.data.row[0]
            //设置头表数据
            this.visitorInfo = {
              ...data,
              visitStartTime: this.$util.format(
                new Date(data.visitStartTime.replace(/-/g, '/')),
                'yyyy-MM-dd hh:mm'
              ),
              visitEndTime: this.$util.format(
                new Date(data.visitEndTime.replace(/-/g, '/')),
                'yyyy-MM-dd hh:mm'
              )
            }
            //设置用户数据
            this.userList = eval('(' + data.visitorPersonArr + ')').map(
              (item) => {
                return {
                  ...item,
                  fileList: item.picData
                    ? [{ content: item.picData.replace(/ /g, '+') }]
                    : [],
                  voucherTypeName: String(
                    this.showPickerTypeList.filter(
                      (item1) => item.voucherType == item1.dicValue
                    )[0].dicDisplay
                  )
                }
              }
            )
            if (!this.token) {
              this.getToken()
            }
          } else {
            this.$toast.fail({
              message: res.msg
            })
          }
        })
      } else {
        //设置默认时间
        let time = new Date()
        this.visitorInfo.visitStartTime = this.$util.format(
          time,
          'yyyy-MM-dd hh:mm'
        )
        this.visitorInfo.visitEndTime = this.$util.format(
          new Date(time.setHours(time.getHours() + 2)), //设置默认加两小时
          'yyyy-MM-dd hh:mm'
        )
      }
    },
    onClickLeft() {
      if (this.editState) {
        //重置
        this.userInfo = { voucherTypeName: '身份证', voucherCode: '' }
        this.editState = false
        this.$refs.userInfo.resetValidation()
      } else {
        this.$router.back(-1)
      }
    },
    //开始时间确认
    onConfirmStart(time) {
      this.visitorInfo.visitStartTime = this.$util.format(
        time,
        'yyyy-MM-dd hh:mm'
      )
      this.showPickerStart = false
    },
    //结束时间确认
    onConfirmEnd(time) {
      this.visitorInfo.visitEndTime = this.$util.format(
        time,
        'yyyy-MM-dd hh:mm'
      )
      this.showPickerEnd = false
    },
    //拜访时间限制范围
    disabledTime(type) {
      switch (type) {
        case 'startMin':
          return new Date()
        case 'startMax':
          if (this.visitorInfo.visitEndTime) {
            if (
              new Date() <
              new Date(this.visitorInfo.visitEndTime.replace(/-/g, '/'))
            ) {
              return new Date(this.visitorInfo.visitEndTime.replace(/-/g, '/'))
            } else {
              return
            }
          }
          break
        case 'endMin':
          if (this.visitorInfo.visitStartTime) {
            return new Date(this.visitorInfo.visitStartTime.replace(/-/g, '/'))
          }
          return
        case 'endMax':
          if (this.visitorInfo.visitStartTime) {
            return new Date(
              Date.parse(this.visitorInfo.visitStartTime.replace(/-/g, '/')) +
                86400000
            )
          }
          return
      }
    },
    //编辑详细信息
    handleEdit(item, i) {
      this.userInfo = { ...item, i }
      this.editState = true
    },
    //点击添加详细信息
    handleAddDetail() {
      this.$refs.visitorInfo.validate().then(() => {
        this.editState = true
      })
    },
    // 验证图片格式是否正确和压缩图片
    async beforeRead(file) {
      const isJpgOrPng =
        file.type === 'image/jpeg' ||
        file.type === 'image/png' ||
        file.type === 'mage/jpg'
      if (isJpgOrPng) {
        const img = await this.$util.iosRotateImg(file)
        if (this.$util.isImgChekSize == 0) {
          this.userInfo.picData = img
        } else if (this.$util.isImgChekSize == 1) {
          this.$toast.fail('您选择的相片小于25K，请重新选择相片！')
        } else {
          this.$toast.fail('您选择的相片超过了7500K，请重新选择相片！')
        }
      } else {
        return new Promise((reject) => {
          this.$toast({
            message: '请选择 jpg, png 格式文件!'
          })
          this.userInfo.picData = ''
          return reject(new Error(false))
        })
      }
    },
    //选择证件类型
    onConfirmType(value) {
      this.userInfo.voucherTypeName = value
      this.userInfo.voucherCode = ''
      this.showPickerType = false
    },
    // 提交详细信息
    onDetailSubmit(values, i) {
      // 获取需要操作的数据
      const [list, info] = [this.userList, this.userInfo]
      //判断是否是为删除，不是删除则是添加或修改
      if (values !== 'del') {
        //有i则修改
        if (info.i !== undefined) {
          list[info.i] = info
        } else {
          list.push(info)
        }
      } else {
        //否则删除
        list.splice(i, 1)
      }
      if (values !== 'del') this.onClickLeft()
    },
    // 保存
    handleSubmit() {
      if (this.userList.length == 0) {
        //验证是否有保存头表
        this.$toast({
          message: '请添加访客信息'
        })
        return
      }
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: '数据保存中...'
      })
      let fun = null
      if (this.visitorInfoId) {
        //修改
        fun = modifyVisitorInfo
      } else {
        // 新增
        fun = addVisitorInfo
      }
      fun({
        ...this.headParams,
        visitorPersonArr: this.userList.map((item) => {
          return {
            ...item,
            fileList: null,
            voucherType: String(
              this.showPickerTypeList.filter(
                (item1) => item.voucherTypeName === item1.dicDisplay
              )[0].dicValue
            )
          }
        })
      })
        .then((res) => {
          this.$toast.clear()
          if (res.code === 200) {
            this.visitorInfoId = res.data.id //设置id,
            this.token = res.data.token //设置token
            this.$util.getRouteParams({
              name: this.$route.name,
              params: {
                vipVisitId: this.visitorInfoId,
                token: res.data.token //设置token
              }
            }) //  设置参数
            this.$toast.success({
              message: res.msg
            })
          } else {
            this.$toast.fail({
              message: res.msg
            })
          }
        })
        .catch(() => {
          this.$toast.clear()
        })
    },
    // 短信分享
    async handleSMS() {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: '链接生成中...'
      })
      let i = 0
      const timer = setInterval(() => {
        i++
        if (this.token) {
          this.$toast.clear()
          //发送短信
          const isiOS = !!navigator.userAgent.match(
            /\(i[^;]+;( U;)? CPU.+Mac OS X/
          ) //ios终端
          const visitorPhoneNbr = String(
            this.userList.map((item) => item.visitorPhoneNbr)
          )
          const hr = `sms:/open?addresses=${visitorPhoneNbr}${
            isiOS ? '&' : '?'
          }body=${this.link}`
          const elink = document.createElement('a')
          elink.style.display = 'none'
          elink.href = hr
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
          clearInterval(timer)
        } else if (i > 60) {
          clearInterval(timer)
          this.$toast.fail({
            message: '分享链接获取失败,请刷新此页面'
          })
        }
      }, 200)
    },
    // 链接分享
    async copyLink() {
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: '链接生成中...'
      })
      let i = 0
      const timer = setInterval(() => {
        i++
        if (this.token) {
          this.$toast.clear()
          this.$copyText(this.link).then(
            () => {
              this.$toast({
                message: '复制成功,请将链接发送给访客'
              })
            },
            () => {
              this.$toast.fail('Can not copy')
            }
          )
          clearInterval(timer)
        } else if (i > 60) {
          clearInterval(timer)
          this.$toast.fail({
            message: '分享链接获取失败,请刷新此页面'
          })
        }
      }, 200)
    },
    // 分享获取token
    getToken() {
      modifyVisitorInfo({
        ...this.visitorInfo,
        visitStartTime: this.visitorInfo.visitStartTime + ':00',
        visitEndTime: this.visitorInfo.visitEndTime + ':00',
        visitorPersonArr: this.userList.map((item) => {
          return {
            ...item,
            fileList: null,
            voucherType: String(
              this.showPickerTypeList.filter(
                (item1) => item.voucherTypeName === item1.dicDisplay
              )[0].dicValue
            )
          }
        })
      })
        .then((res) => {
          if (res.code === 200) {
            this.visitorInfoId = res.data.id //设置id,
            this.token = res.data.token //设置token
            this.$util.getRouteParams({
              name: this.$route.name,
              params: {
                vipVisitId: this.visitorInfoId,
                token: res.data.token //设置token
              }
            })
          } else {
            this.$toast.fail({
              message: `分享链接获取失败`
            })
          }
        })
        .catch((err) => {
          console.log('err: ', err)
        })
    }
  },
  computed: {
    link() {
      return `您好，${this.myUserInfo.name} 邀请您于 ${this.visitorInfo.visitStartTime} 至 ${this.visitorInfo.visitEndTime} 来访，请点击链接获取通行码 \n${this.$util.QR_CODE_LINK_IP}/visitor/passCertificate?key=${this.token} \n如果上传人脸，可通过人脸识别进入。\n如果填写车牌，可通过车闸识别进入。`
    },
    headParams() {
      return {
        //用户填写的信息
        ...this.visitorInfo,
        visitStartTime: this.visitorInfo.visitStartTime + ':00',
        visitEndTime: this.visitorInfo.visitEndTime + ':00',
        visitType: '1',
        visitorInfoId: this.visitorInfoId,
        sts: this.visitorInfoId,
        //被访人用户信息
        unitId: this.unitId,
        phoneNbr: this.myUserInfo.phoneNbr,
        name: this.myUserInfo.name,
        toOpenid: this.myUserInfo.openId
      }
    },
    ...mapState({
      myUserInfo: (state) => state.user.userInfo, // 用户信息
      unitId: (state) => state.user.unitId
    })
  },
  created() {
    this.init()
  },
  watch: {
    editState(newVal) {
      document.body.style.overflowY = newVal ? 'hidden' : 'auto'
    }
  },
  beforeRouteEnter(to, from, next) {
    if (!['VIPappointment', 'visitorLog'].includes(to.name)) {
      //清除local storage中保存的参数
      vm.$ls.remove(from.name)
    }
    next()
  }
}
</script>

<style lang="less" >
.vip-appointment {
  .edit-form {
    margin: 17px 10px;
    .tag-title {
      font-size: 15px;
